body {
  background-color: rgba(244, 244, 244, .918);
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

header {
  background-color: rgba(8, 3, 77, .733);
  border-radius: 6px;
}

h2 {
  color: #fff;
  text-align: center;
  font-size: 3em;
}

.search-form {
  text-align: center;
  margin: 20px 0;
  padding-bottom: 30px;
}

.search-form input {
  border: 2px solid rgba(244, 244, 244, .918);
  border-radius: 6px;
  padding: 5px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  padding: 15px;
  display: flex;
}

.image-card {
  width: 30%;
  height: auto;
  border-radius: 6px;
  padding: 15px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .18);
}

.image-card img {
  max-width: 100%;
  height: auto;
  border-radius: 6px;
}

.photo-info {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: rgba(8, 3, 77, .733);
  border: none;
  border-radius: 6px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

.load-more:hover {
  background-color: #27ae60;
}

